<template>
  <div id="editBlog">
   <h2>修改博客</h2>
    <form v-if="!submited">
      <label for="">博客标题</label>
      <input type="text" v-model="blog.title" required>
      <label for="">博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label for="">Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label for="">Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label for="">React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label for="">Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories">
      </div>
      <label for="">作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">
          {{author}}
        </option>
      </select>
      <button @click.prevent="post">修改博客</button>
    </form>
    <div v-if="submited">
      <h3>博客修改成功</h3>
    </div>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容</p>
      <article>
        {{blog.content}}  
      </article>
      <p>博客分类</p>
      <ul>
        <li v-for="category in blog.categories">
          {{category}}
        </li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'editBlog',
  data(){
    return {
      id:this.$route.params.id,
      blog:{
        categories:[]
      },
      authors:["Hemiah","Henry","Bucky"],
      submited:false
    }
  },
  methods:{
    post:function(){
      if(!this.blog.title){
        alert("随便写下标题嘛");
        return;
      }
      if(!this.blog.content){
        alert("随便写点内容撒");
        return;
      }
      //var url = "http://jsonplaceholder.typicode.com/posts";
      var url = "/posts/";
       //this.$http.put(url+this.id+'.json',this.blog)
       axios.put(url+this.id+'.json',this.blog)
       .then((data)=>{
          this.submited = true;
       })
    },
    featchData:function(){
      var url = "/posts/";
      axios.get(url+this.id+".json").then(function(data){
        return data.data
      }).then((data)=>{
        var cateObj = data.categories || {};
        var array = [];
        for(let key in cateObj){
          array.push(cateObj[key])
        }
        this.blog = data;
        this.blog.categories = array;
      })
    }
  },
  created(){
    this.featchData()

  }
}
</script>
<style scoped>
  #add-blog *{
    box-sizing: border-box;
  }
  #add-blog{
    margin: 20px autp;
    max-width: 600px;
    padding: 20px;
  }
  label{
    display: block;
    margin: 20px 0 10px;
  }
  input[type='text'],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }
  textarea{
    height: 200px;
  }
  #checkboxes label{
    display: inline-block;
    margin-top: 0px;
  }
  #checkboxes input{
    display: inline-block;
    margin-right: 10px;
  }
  button{
    display: block;
    margin: 20px 0px;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
  }
  #preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
  }
  h3{
    margin-top: 10px;
  }
  article{
    word-wrap:break-word;
  }
</style>



