<template>
  <div v-theme:column="'narrow'" id="showBlogs">
  	<h1>博客总览</h1>
  	<input type="text" v-model="search" name="" 
  	placeholder="搜索">
  	<div v-for="blog in filterBlogs" class="single-blog" :key="blog.id">
  		<router-link v-bind:to="'/blog/' + blog.id">
  		 <h2 v-rainbow>{{blog.title | to-upperCase}}</h2>
  		 <article>
  		 	{{blog.content | sinppet}}
  		 </article>
  		</router-link>
  	</div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'showBlogs',
  data(){
  	return{
  		blogs:[],
  		search:""
  	}
  },
  created(){
  	//var url = "../../static/posts.json";
  	var url ="/posts.json";
  	//this.$http.get(url).then(function(data){
  	axios.get(url).then(function(data){
  		return  data.data;
  	}).then((data)=>{
		var blogsArray = [];
		for(let key in data){
			var obj = data[key]
			obj['id'] = key;
			blogsArray.push(obj)
		}
		this.blogs = blogsArray
  	});
  },
  computed:{
  	filterBlogs:function(){
  		return this.blogs.filter((blog) => {
  			
  			return blog.title.match(this.search)
  		});
  	}
  },
  filters:{
  	'to-upperCase':function(value){
  		if(!value){
  			value = "!!!!"
  		}
		return value.toUpperCase();
	}
	// toUpperCase(value){
	// 	return value.toUpperCase();
	// }
  },
  directives:{
	'rainbow':{
  		bind(el,binding,vnode){
  			el.style.color = "#"+Math.random().toString(16).slice(2,8);
  		}
  	}
  }

}
</script>

<style>
#showBlogs{
	max-width: 800px;
	margin: 0 auto;
}
.single-blog{
	padding: 20px;
	margin:20px 0;
	box-sizing: border-box;
	background: #eee;
	border: 1px dotted #aaa;
}
#showBlogs a{
	color: #444;
	text-decoration: none;
}
input[type='text']{
	padding: 8px;
	width: 100%;
	box-sizing: border-box;
}
</style>



