<template>
	<div id="singleBlog">
		<h1>{{blog.title}}</h1>
		<article>
			{{blog.content}}
		</article>
		<p>博客分类</p>
	      <ul>
	        <li v-for="category in blog.categories" :key="category">
	          {{category}}
	        </li>
	      </ul>
		<p>作者:{{blog.author}}</p>
		<button @click="deleteSingleBlog()">删除</button>
		<router-link tag="button" v-bind:to="'/edit/' + id">
			编辑
		</router-link>

	</div>
</template>
<script>
	import axios from 'axios'
	export default{
		name:"singleBlog",
		data(){
			return {
				id: this.$route.params.id,
				blog:{}
			}
		},
		created(){
			var url = "/posts/";
			//this.$http.get(url+this.id+".json").then(function(data){
			axios.get(url+this.id+".json").then(function(data){
				return data.data
				//this.blog = data.body;
			}).then((data) => {
				this.blog = data
			})
		},
		methods:{
			deleteSingleBlog(){
				var url = "/posts/";
				if(confirm("确定删除它吗?")){
					axios.delete(url+this.id+".json").then(response=>{
						alert("删除成功")
						this.$router.push({path:'/'})
					})
				}
				
			}
		}
	}
</script>
<style>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}	
	article{
		word-wrap:break-word;
	}
</style>


